import { BASE_URL } from './../../services/constants.service';
import {  UserProductModal } from './../sub-options-with-icons/sub-option-one/sub-option-one';
import { Component, OnInit, ViewChild } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ModalController,
  Slides,
  ViewController,
  ToastController,
  AlertController
} from "ionic-angular";
import { Storage } from "@ionic/storage";
import { ProductProvider } from '../../providers/store/product';
/**
 * Generated class for the UserProductsListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-products-list',
  templateUrl: 'user-products-list.html',
})
export class UserProductsListPage {

  products: any = [];
  productsbackup:any = [];
  token: any;
  searchTerm: any;
  showSearch:any;

  constructor(
    public modalCtrl: ModalController,
    private storage: Storage,
    public navCtrl: NavController,
    public navParams: NavParams,
    public toastCtrl: ToastController,
    private alertCtrl: AlertController,
    private productProvider: ProductProvider
  ) {
    
    this.productProvider.getUserProductList().subscribe((resp: any) => {

      if (resp.status) {
        this.products = resp.message;
        var temp = [];
        this.products.forEach(element => {
          let item = {
            id : element.id,
            name: element.pr_name,
            image: this.getImage(element.pr_image),
            rating: parseFloat(element.pr_rating),
            price: parseFloat(element.pr_sale_amount),
            status: parseInt(element.pr_status)
          };
          temp.push(item);
        });
        console.clear()
        console.log(temp);
        this.productsbackup = this.products = temp;
      }
      else {
        console.log(resp);
      }
    });
  }

  filterStore(searchTerm) {
    this.products = this.productsbackup;
  return this.products.filter(item => {
    return item.name.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1;
  });
}

  
  setFilteredItems(){
    this.products = this.filterStore(this.searchTerm); 
 }

  getImage(img) {
    return BASE_URL + "/uploads/stores/products/" + img;
  }
  toArray(arr) {
    return arr
      .replace(/,/gi, " ")
      .trim()
      .split(" ");
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProductsListPage');
  }

  goToCart() {
    this.navCtrl.push('CartPage');
  }

  presentPopover(myEvent: Event) {
    // let popover = this.popoverCtrl.create(PopoverPage);
    // popover.present({
    //   ev: myEvent
    // });
  }

  search() {

  }

  goToProductDetail(data: any) {
    console.log(data.id);
    console.log(this.token);
    this.navCtrl.push(UserProductModal, { id: data.id }); 
  }

  presentFilter() {
    let modal = this.modalCtrl.create("ProductsFilterPage");
    modal.present();

    modal.onDidDismiss((data: any[]) => {
      if (data) {
        // play with data here
      }
    });
  }

  sortBy() {
    let alert = this.alertCtrl.create();
    alert.setTitle('Sort Options');

    alert.addInput({
      type: 'radio',
      label: 'Relevance',
      value: 'relevance',
      checked: true
    });
    alert.addInput({
      type: 'radio',
      label: 'Popularity',
      value: 'popular'
    });
    alert.addInput({
      type: 'radio',
      label: 'Low to High',
      value: 'lth'
    });
    alert.addInput({
      type: 'radio',
      label: 'High to Low',
      value: 'htl'
    });
    alert.addInput({
      type: 'radio',
      label: 'Newest First',
      value: 'newest'
    });

    alert.addButton('Cancel');

    alert.addButton({
      text: 'OK',
      handler: (data: any) => {
        console.log(data);

        let navTransition = alert.dismiss();

        // start some async method
        setTimeout(() => {
          navTransition.then(() => {
            this.navCtrl.push('CartPage');
          });
        }, 3000);

        // return false;
      }
    });

    alert.present().then(() => {
      console.log("=====alert---present");
    });
  }

}



